raul-page-loader {
  display: block;
  font-size: 0.875rem;
}

raul-page-loader .r-page-loader__container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  background-color: rgba(55, 71, 79, 0.5);
}

raul-page-loader .r-page-loader__container .r-page-loader__spinner {
  opacity: 1;
  height: 3rem;
  width: 3rem;
  border-radius: 9999px;
  border-width: 4px;
  border-color: #fff;
  border-right-color: #0076CC;
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}